import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

let base = `${process.env.VUE_APP_BASEURL}` // 动态获取二级目录

const router = new Router({
    mode: '',
    // base: base,
    routes: [
        {
            path: '/',
            redirect: '/home',
            name: 'index',
            meta: {
                requireAuth: false
            },
            component: () => import('views/index/app.vue'),
            children: [
                {
                    path: '/home',
                    name: 'home',
                    meta: {
                        requireAuth: false,
                    },
                    component: () => import('views/index/home')
                },
                {
                    path: '/registration',
                    name: 'registration',
                    meta: {
                        requireAuth: false,
                    },
                    component: () => import('views/index/registration')
                },
                {
                    path: '/calendar',
                    name: 'calendar',
                    meta: {
                        requireAuth: false,
                    },
                    component: () => import('views/index/calendar')
                },
                {
                    path: '/success',
                    name: 'success',
                    meta: {
                        requireAuth: false,
                    },
                    component: () => import('views/index/success')
                },
                {
                    path: '/my',
                    name: 'my',
                    meta: {
                        requireAuth: false,
                    },
                    component: () => import('views/index/my')
                },
                {
                    path: '/detail',
                    name: 'detail',
                    meta: {
                        requireAuth: false,
                    },
                    component: () => import('views/index/detail')
                },
                {
                    path: '/evaluation',
                    name: 'evaluation',
                    meta: {
                        requireAuth: false,
                    },
                    component: () => import('views/index/evaluation')
                }
            ]
        }
    ],
    scrollBehavior(to, from, savedPosition) {
        if (savedPosition) {
            return savedPosition
        } else {
            return {
                x: 0,
                y: 0
            }
        }
    }
});
// 路由拦截器
router.beforeEach((to, from, next) => {
    if (to.meta.requireAuth) { // 添加该字段，表示进入这个路由是需要登录的
        if (localStorage.getItem('userInfo') && JSON.parse(localStorage.getItem('userInfo')).userId && JSON.parse(localStorage.getItem('userInfo')).session) {
            next();
        }
        else {
            next({
                path: '/login',
                query: { redirect: to.fullPath }
            })
        }
    }
    else {
        next();
    }
})

export default router;
